import { CurrencyMaskModule } from 'ng2-currency-mask';
import { TextMaskModule } from 'angular2-text-mask';

import { NgModule } from '@angular/core';
import { HttpClientModule } from '@angular/common/http';
import { PopupModalModule } from 'app/shared/popup/popup-modal.module';
import { ModalModule } from 'ng2-bootstrap/modal';
import { FormsModule, ReactiveFormsModule } from '@angular/forms';
import { CommonModule } from '@angular/common';

import { SharedComponentModule } from './../../../shared/shared-component.module';
import { TaxaEntregaRoutingModule } from './taxa-entrega-routing.module';
import { TaxaEntregaPopupModalComponent } from './popup/taxa-entrega-popup-modal.component';
import { ListarTaxaEntregaComponent } from './listar/listar-taxa-entrega.component';
import { TaxaEntregaService } from './taxa-entrega.service';

@NgModule({
  imports: [
    CommonModule,
    FormsModule,
    ModalModule.forRoot(),
    SharedComponentModule,
    ReactiveFormsModule,
    HttpClientModule,
    PopupModalModule,
    CurrencyMaskModule,
    TaxaEntregaRoutingModule
  ],
  declarations: [
    ListarTaxaEntregaComponent,
    TaxaEntregaPopupModalComponent
  ],
  providers:[
    TaxaEntregaService
  ]
})
export class TaxaEntregaModule { }



