import { TaxaEntregaService } from './../taxa-entrega.service';
import { Component, OnInit, ViewChild } from '@angular/core';
import { Router } from '@angular/router';


import { EventEmitterService } from 'app/shared/event/event-emitter.services';
import { AbstractBaseComponent } from 'config/core/abstract-base.component';
import { TrataErrorService } from 'services/trata-error.service';

import { TaxaEntregaDTO } from './../../../../../models/domain/taxa-entrega.dto';

@Component({
  templateUrl: './listar-taxa-entrega.component.html',
})
export class ListarTaxaEntregaComponent extends AbstractBaseComponent implements OnInit {

  @ViewChild('excluirModal') popupExclusao;
  @ViewChild('changeSatusModal') popupChangeStatus;

  item: TaxaEntregaDTO;
  items: TaxaEntregaDTO[];

  constructor(
    public taxaService: TaxaEntregaService,
    public router: Router) {
    super();
  }

  ngOnInit() {
    this.carregarTodos();
  }

  selecionarItem(item: TaxaEntregaDTO) {
    this.item = item;
    this.popupExclusao.show();
  }

  selecionarItemChangeStatus(item: TaxaEntregaDTO) {
    this.item = item;
    this.popupChangeStatus.show();
  }

  changeStatus() {
    this.taxaService.changeStatus(this.item)
      .subscribe(res => {
        EventEmitterService.emitirMensagemToasterSucesso('Taxa de Entrega', 'O status da taxa de entrega foi alterado com sucesso!!!')
        this.carregarTodos()
      }, error => TrataErrorService.tratarError(error));
  }

  excluir() {
    this.taxaService.excluir(this.item)
      .subscribe(res => {
        EventEmitterService.emitirMensagemToasterSucesso('Taxa de Entrega', 'A taxa de entrega foi excluída com sucesso!!!')
        this.carregarTodos()
      }, error => TrataErrorService.tratarError(error));
  }

  private carregarTodos() {
    this.taxaService.findAll().subscribe(response => {
      this.items = response
    }, error => TrataErrorService.tratarError(error));
  }

}



